import React from 'react';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import Body from './components/Body/Body';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ticketsAll: [],
    }
    this.addTicket = this.addTicket.bind(this);
    this.deleteTicket = this.deleteTicket.bind(this);
  }
  
  addTicket(key) {
    var array = [];
    if (this.state.ticketsAll) {
      var temporary = array.concat(this.state.ticketsAll);
      array = temporary;
    }
    var fiveNumber = [];
    var twoNumber = [];
    var Ticket = {
      ticketNumber: '',
      number: {
        fiveNumber: [],
        twoNumber: []
      }
    }
    for (var i = 0; i < 5; i++){
      const number = Math.floor((Math.random() * 50) + 1);
      (fiveNumber.includes(number) ? i-- : fiveNumber.push(number));
    }
    fiveNumber.sort(function (a, b) { return a - b });

    for (var j = 0; j < 2; j++){
      const number = Math.floor((Math.random() * 10) + 1);
      (twoNumber.includes(number) ? j-- : twoNumber.push(number));
    }
    twoNumber.sort(function (a, b) { return a - b });
    
    Ticket.ticketNumber = key;
    Ticket.number.fiveNumber = fiveNumber;
    Ticket.number.twoNumber = twoNumber;
    array.push(Ticket);
    
    this.setState({
      ticketsAll: array
    })
  }

  deleteTicket(key) {
    const filteredTicket = this.state.ticketsAll.filter(ticket=>ticket.ticketNumber !== key);
    this.setState({
      ticketsAll: filteredTicket
    })

  }
  render() {
    return (
      <div>
        <Navbar/>
        <Body tickets={this.state.ticketsAll} deleteTicket={this.deleteTicket} addTicket={this.addTicket} />
      </div>
    )
  }

}


export default App;